<template>
  <div class="bedlist_container">
    <ul v-load-more="loaderMore" v-if="bedListArr.length" type="1">
      <router-link :to="{path: 'bed', query:{id: item.id}}" v-for="item in bedListArr" tag='li' :key="item.id" class="bed_li">
        <section class="bed_img">
          <img v-if="item.Gender == 1" src="../assets/icon/woman.png" />
          <img v-else src="../assets/icon/man.png" />
        </section>
        <div class="bed_right">
          <header>
            <h4 class="bed_title ellipsis" :class="'lv_'+ item.lv">{{item.userName}}</h4>
            <ul class="bed_detail_ul">
              <li v-if="item.detailDD == 1"></li>
              <li v-if="item.detailYC == 1"></li>
              <li v-if="item.detailWC == 1"></li>
              <li v-if="item.detailGM == 1"></li>
              <li v-if="item.detailGL == 1"></li>
              <li v-if="item.detailDT == 1"></li>
              <li v-if="item.detailBW == 1"></li>
              <li v-if="item.detailBZ == 1"></li>
            </ul>
          </header>
          <h5 class="rating_num">
            <section class="rating_num_left">
              <span class="rating_feeNo">:{{item.feeNo}}</span>
              <span class="rating_age">{{item.age}}</span>
              <span class="rating_inDate">{{item.inDate}}</span>
            </section>
          </h5>
          <h5 class="fee_distance">
            <p class="fee">
              <span>{{item.tel}}</span>
              <span class="segmentation">/</span>
              :{{item.Dr}}
            </p>
            <p class="distance">{{item.bedNumber}}</p>
          </h5>
        </div>
      </router-link>
      <!--<li class="bed_li">
        <section class="bed_img">
          <img src="../assets/icon/woman.png" alt="">
        </section>
        <div class="bed_right">
          <header>
            <h4 class="bed_title ellipsis lv_1"></h4>
            <ul class="bed_detail_ul">
              <li></li>
              <li></li>
              <li></li>
              <li></li>
              <li></li>
              <li></li>
              <li></li>
            </ul>
          </header>
          <h5 class="rating_num">
            <section class="rating_num_left">
              <span class="rating_feeNo">:409813</span>
              <span class="rating_age">19</span>
              <span class="rating_inDate">2017/06/22</span>
            </section>
          </h5>
          <h5 class="fee_distance">
            <p class="fee">
              <a href="tel:13397016910">13307978888</a>
              <span class="segmentation">/</span>
              :
            </p>
            <p class="distance"></p>
          </h5>
        </div>
      </li>
      -->
    </ul>
    <ul v-else>
      <li v-for="item in 10" :key="item">
        
      </li>
    </ul>
    <p v-if="touchend" class="empty_data"></p>
  </div>
</template>

<script>
  import {bedList} from '../service/getData'
  import {loadMore} from './load'

  export default {
    data() {
      return {
        bedListArr:[],  //
        preventRepeatReuqest:false,  //
        showLoading: true,
        touchend: false, //
      }
    },
    mounted(){
      this.initData();
    },
    components:{
    },
    props:['StationID'],
    mixins:[loadMore],
    computed:{},
    updated(){
      // console.log(this.supportIds, this.sortByType)
    },
    methods: {
      async initData() {
        //
        let res = await bedList(this.StationID);
        this.bedListArr = [...res];
        if (res.length < 20) {
          this.touchend = true;
        }
      },
      //
      async loaderMore() {
        if (this.touchend) {
          return
        }
        //
        if (this.preventRepeatReuqest) {
          return
        }
        this.preventRepeatReuqest = true;

        //20
        this.offset += 20;
        let res = await bedList(this.StationID);
        this.hideLoading();
        this.bedListArr = [...this.bedListArr, ...res];
        //20
        if (res.length < 20) {
          this.touchend = true;
          return
        }
        this.preventRepeatReuqest = false;
      },
      //
      async listenPropChange(){
        this.showLoading = true;
        let res = await bedList(this.StationID);
        this.hideLoading();
        //
        this.bedListArr = [...res];
      },
      hideLoading(){
        this.showLoading = false;
      },
    },
    watch:{
      //StationID
      StationID:function (value) {
        this.listenPropChange();
      }
    }
  }
</script>
<style>
  .bed_right .rating_num {
    height: 0.6rem;
    margin-top: 0.3rem;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
    -ms-flex-pack: justify;
    justify-content: space-between;
  }
  .bed_right .rating_num .rating_num_left {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: start;
    -ms-flex-pack: start;
    justify-content: flex-start;
  }
  .bed_right .rating_num .rating_num_left .rating_feeNo {
    font-size: .4rem;
    color: #ff6000;
  }
  .bed_right .rating_num .rating_num_left .rating_age {
    margin: 0 .2rem;
  }
  .bed_right .rating_num .rating_num_left .rating_inDate {
    color: #333;margin: 0 .2rem;
  }
  .bed_right .fee_distance {
    margin-top: .4rem;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
    -ms-flex-pack: justify;
    justify-content: space-between;
    font-size: .5rem;
    color: #333;
  }
  .bed_right .fee_distance .distance {
    background-color: #3aa373;
    color: #fff;
    padding: 0 .2rem;
    font-size:.5rem;
    -webkit-transform: scale(.8);
    transform: scale(.8);
    border: 1px solid #42a873;
    border-radius: .15rem;
  }
  .bed_right .fee_distance .fee {
    font-size: .5rem;
    color: #666;
  }
  .loader_more{
    font-size:.6rem;
    line-height:3;
    text-align: center;
    color: #999;
  }
  .empty_data{
    font-size:.5rem;
    color:#666;
    text-align: center;
    line-height: 2rem;
  }
</style>



